.wrapper {
  margin-left: auto;
  margin-right: auto;

  width: 960px;
}

.header {
  padding: 0 10px;
  margin-bottom: 5px;
}

.content {
  display: flex;
  justify-content: space-between;
}

.gameItemWrapper img {
  width: 172px;
}

@media all and (min-width: 1776px) {
  .wrapper {
    width: 1461px;
  }

  .gameItemWrapper img {
    width: 223px;
  }
}